import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';




const App = () => { 

const groupID = 0;
const groupToken = "";
const MessagesFromGroup = false;
const UserDate = {};
const UserID = "";
groupID = bridge.send("VKWebAppAddToCommunity");

groupToken = bridge.send("VKWebAppGetCommunityToken", {"app_id": 7617611, "group_id": groupID, "scope": "messages"});

MessagesFromGroup = bridge.send("VKWebAppAllowMessagesFromGroup", {"group_id": groupID, "key": "dBuBKe1kFcdemzB"});

UserDate = bridge.send("VKWebAppGetUserInfo");
UserID = UserDate.id;

if (groupToken != "")
{
	bridge.send("VKWebAppCallAPIMethod", {"method": "messages.send", "request_id": "32test", "params": {"user_id": UserID, "v":"5.124", "access_token":groupToken}});

}

}
export default App;




